* {
	margin: 0;
	padding: 0;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}
a {
	text-decoration: none;
}
li {
	list-style: none;
}

html {
	box-sizing: border-box;
	font-size: 62.5%;
}
@media only screen and (max-width: 68.75em) {
	html {
		font-size: 50%;
	}
}

body {
	font-family: "Nunito Sans", sans-serif;
	font-weight: 400;
	line-height: 1.6;
}
